import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"

import { PieChart } from 'react-charts-d3';
import { BarChart } from 'react-d3';



const data = [
    {"apples": 10, "oranges": 5, "pears": 15}
  ];

  var barData = [
    {label: 'A', value: 5},
    {label: 'B', value: 6},
    {label: 'F', value: 7}
  ];
const Histogram = ({ siteTitle }) => (
    <div
      style={{
        margin: `0 auto`,
        maxWidth: 960,
        padding: `1.45rem 1.0875rem`,
      }}
    >
      <PieChart data={data} />
      <BarChart
                  data={barData}
                  width={500}
                  height={200}
                  fill={'#3182bd'}
                  title='Bar Chart'
                />
    </div>
)

Histogram.propTypes = {
  siteTitle: PropTypes.string,
}

Histogram.defaultProps = {
  siteTitle: ``,
}

export default Histogram
